import React, {useEffect} from "react";
import wrapper from "../commons/WebComponentWrapper";

const dispatchedEvents = ["ABOUT#clicked"];

const About = function(props, ref) {
    return view(props, ref);
}

const view = (props, ref) => {
    return (<React.Fragment ref={ref}> 
        <div>About Page</div>
        <button onClick={() => {
            props.dispatchEvent("ABOUT#clicked", { message: 'Button was clicked' });
        }}>About</button>
        <div>{props.message}</div>
    </React.Fragment>);
}

const styles = `
    
`
const props = [{
    name: "message",
    defaultValue: "Click me",
    type: "string"
}]

const WrappedComponent = wrapper(React.forwardRef(About), props);
export {WrappedComponent, styles, props, dispatchedEvents};